<!--
 * @Author: Marlon
 * @Date: 2020-02-18 10:29:39
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-24 14:20:45
 * @Description: 数据库结构同步
 -->
 <template>
  <div class="db-structure">
    <div class="db-structure-top">
      <div class="area-query">
        <label for>项目：</label>
        <el-select v-model="proVal" filterable placeholder="请选择" size="small">
          <el-option
            v-for="item in proList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="area-table">
        <TableDefault
          TId="1"
          :TTitle="tableTitle"
          :TData="tableData"
          :TIsShowPage="true"
          :TEdit="TEdit"
          @editBtnCb="editBtnCb"
          @clickRow="clickRow"
        ></TableDefault>
      </div>
    </div>
    <span class="db-structure-mid" @click="handleFold">
      <i v-show="fold" class="el-icon-arrow-down up-dow"></i>
      <i v-show="!fold" class="el-icon-arrow-up up-dow"></i>
    </span>
    <div v-show="fold" class="db-structure-bot">
      <div class="bot-title">数据库"北京-ods"的维护日志：</div>
      <div class="bot-table">
        <TableDefault TId="2" :TTitle="tableTitleBot" :TData="tableDataBot" :TIsShowPage="true"></TableDefault>
      </div>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      :FDForm="FDForm"
      FDWidth="500"
      :FDFoot="FDFoot"
      @editBtnCb="editBtnCbDiglog"
    ></FormDialog>
    <SqlDialog></SqlDialog>
  </div>
</template>
 
 <script>
import mixin from "mixin";
import API from "api/index";
import { openUrl } from "utils/jump";

export default {
  mixins: [mixin],
  data() {
    return {
      fold: false,
      //   项目名称
      proVal: "",
      //   项目列表
      proList: [
        {
          value: "",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "杭州"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        width: "450",
        fixed: "right",
        editTypes: [
          {
            label: "设置数据源",
          },
          {
            label: "生成建库SQL"
          },
          {
            label: "差异对比"
          },
          {
            label: "生成差异SQL"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "所属项目"
        },
        {
          prop: "date",
          label: "共维护更新次数"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      tableTitleBot: [
        {
          prop: "name",
          label: "更新SQL"
        },
        {
          prop: "date",
          label: "维护时间"
        },
        {
          prop: "address",
          label: "负责人"
        }
      ],
      tableDataBot: [
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        }
      ],
      // 可跳转的值
      TClickItem: [
        {
          link: "/",
          cells: ["address"]
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [
        {
          type: "select",
          label: {
            name: "所属项目",
            width: "120"
          },
          width: "200",
          defaultModel: "",
          placeholder: "",
          option: [
            {
              label: "海淀",
              value: "haidian"
            }
          ]
        },
        {
          type: "input",
          label: {
            name: "中文名称",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "input",
          label: {
            name: "库名",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "textarea",
          label: {
            name: "备注",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: "",
          autosize: {
            min: 2,
            max: 4
          }
        }
      ],
      // 底部操作配置(弹出框)
      FDFoot: {
        button: [
          {
            type: "button",
            label: "添加数据源",
            cb: () => {
              // TODO 数据源管理地址(系统级)
              openUrl({
                url: "http://www.baidu.com",
                obj: { a: 123 }
              });
            }
          },
          {
            type: "ok",
            label: "确定",
            cb: () => {
              console.log("确定");
            }
          }
        ]
      },
      // 临时存储点击的按钮信息
      editBut_: ""
    };
  },
  methods: {
    handleFold() {
      this.fold = !this.fold;
    },
    // 表格-回调
    editBtnCb(editItem) {
      this.editBut_ = editItem;
      // console.log(editItem);
      let text = editItem.label;
      switch (text) {
        case "差异对比":
          this.showDialog({ type: text });
          return;
        case "生成差异SQL":
          this.showDialog({ type: text });
          return;
        default:
          break;
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type, initDig = null }) {
      this.FDTitle = "设置数据源";
      this.FDVisible = true;
      this.FDForm = [
        {
          type: "select",
          label: {
            name: "数据源",
            width: "120"
          },
          width: "200",
          defaultModel: "",
          placeholder: "",
          option: [
            {
              label: "海淀",
              value: "1"
            }
          ]
        }
      ];
      if (initDig && JSON.stringify(initDig) !== "{}") {
        for (const key in initDig) {
          if (initDig.hasOwnProperty(key)) {
            // TODO 优化
            this.FDForm.forEach(item => {
              item["key"] = key;
              item["defaultModel"] = initDig[key];
            });
          }
        }
      }
      // TODO 获取下拉数据
    },
    // 弹出框-回调
    editBtnCbDiglog(obj, formReq) {
      this.FDVisible = false;
      if (obj === "确定") {
        // console.log(this.FDTitle);
        switch (this.FDTitle) {
          case "新增库":
            // TODO 发请求
            console.log(formReq);
            return;

          default:
            return;
        }
      }
    },
    // 点击行
    clickRow({ row, column, id }) {
      // 将“操作”列抽象为多列
      console.log(this.editBut_, row, column, id, "抽象列");
      // 初始化赋值抽象列
      switch (this.editBut_.label) {
        case "设置数据源":
          // TODO
          // const {a} = row;
          this.showDialog({
            type: this.editBut_.label,
            initDig: {
              sourceVal: "1" // a
            }
          });
          return;
        case "生成建库SQL":
          
          return;

        default:
          console.warn("未知抽象列");
          return;
      }
    }
  },
  mounted() {}
};
</script>
 
<style lang="scss" scoped>
.db-structure {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  &-top {
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    border: 1px solid red;
    flex: 1;
    overflow: auto;
    .area-query {
      float: right;
      margin: 0px 30px;
    }
    .area-table {
      margin-top: 40px;
      width: 100%;
      height: calc(100% - 40px);
      overflow: auto;
    }
  }
  &-mid {
    display: inline-block;
    height: 30px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid red;
    text-align: center;
    .up-dow {
      font-weight: bolder;
      font-size: 30px;
    }
  }
  &-bot {
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    border: 1px solid red;
    flex: 1;
    overflow: auto;
    .bot-title {
      float: left;
      height: 32px;
      line-height: 30px;
      margin: 0px 30px;
      font-size: 15px;
      margin-top: 7px;
    }
    .bot-table {
      margin-top: 40px;
      width: 100%;
      height: calc(100% - 40px);
      overflow: auto;
    }
  }
}
</style>



